<template>
  <div class="">
    <div class="row">
      <div class="col-md-5 text-center nicefont">
        <h4>Welcome to Youtube Rater</h4>
      </div>
      <button v-on:click="getTravels">Get my first Travels</button>

    </div>
    <div class="col-md-12 text-left">
      <h5 v-bind:key="trip.id" v-for="trip in trips">
        {{trip.title}}
      </h5>
    </div>
  </div>
</template>
<script>
  export default {
      name: 'home',
      components: {
          
      },
      data(){
        return{
          trips : [],
        }
      },
      methods:{
        getTravels(){
          this.$ajax.get(process.env.VUE_APP_BACKEND_SERVER + "/api/trips/trip/")
          .then(res => (this.trips = res.data))
          // .then(res => (console.log(res.data)))
          .catch(err => console.log(err));
        }
      },
  }
</script>
